@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.button-calculator {
    margin: 0.3rem;
    font-size: 1.3rem;
    border: none;
}

.button-calculator.col-1 {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
}

.button-calculator.col-2 {
    border-radius: 2rem;
    grid-column-start: 1;
    grid-column-end: 3;
    text-align: left;
    padding-left: 1.5rem;
}

.button-calculator:disabled {
    opacity: 0.2;
    cursor: not-allowed;
}
.container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.calculator {
    display: block;
    padding: 0 6px 6px;
    width: 307px;
    height: 464px;
    background-color: #000;
    border-radius: .5rem;
}

.calculator-header {
    text-align: right;
    padding: 1rem .6rem 0 0;
    max-width: 294px;
    min-height: 90px;
    height: auto;
    word-wrap: break-word;
    color: #fff;
    font-size: 3rem;
    transition: font-size .5s;
}

.calculator-body {
    display: grid;
}

.calculator-body .row-ac {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
}

.calculator-body .grid-numbers {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-row-start: 2;
}

.calculator-body .col-operators {
    display: grid;
    grid-row-start: 1;
    grid-row-end: 3;
}
.history {
    display: block;
    padding: 0 6px 6px;
    width: 307px;
    height: 464px;
    background-color: #000;
    border-radius: .5rem;
}

.history-header {
    text-align: right;
    padding: 1rem .6rem 0;
    max-width: 294px;
    min-height: 90px;
    height: auto;
    word-wrap: break-word;
    color: #fff;
    font-size: 3rem;
    transition: font-size .5s;
}

.history-body {
    color: #fff;
    text-align: right;
    padding: 1rem 6px;
    overflow: auto;
    max-height: 350px;
}

.history-equation {
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1rem;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid #ccc;
}

.history-equation span:first-child {
    font-size: .8rem;
    display: flex;
    flex-direction: column;
    text-align: left;
}

.history-equation span:last-child {
    max-width: 200px;
    font-size: 1rem;
    word-wrap: break-word;
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background: #505050;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;   
}

button { 
    cursor: pointer;
}

.bg-warning { 
    background-color: #fe9505; 
}

.bg-dark { 
    background-color: #333333; 
}

.bg-gray { 
    background-color: #a5a5a5; 
}

.text-dark { 
    color: #000; 
}

.text-white { 
    color: #fff; 
}

.container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.nav-menu {
    margin-bottom: 1rem;
}

.nav-menu a {
    color: #fe9505;
    font-size: 1rem;
    text-decoration: none;
}
